.wrapp {
    display: flex;
    flex-direction: column;
    align-items: center;

    height: calc(100vh - 190px);
    margin: 3% 0;

    text-align: center;
}

.inner {
    padding: 20px;

    border: 2px solid #f1f1f2;
    border-radius: 20px;
}

.formWrapp {
    display: flex;
    flex-direction: column;
    gap: 20px;

    width: 300px;
    margin: 0 auto;
}

@media only screen and (max-width: 330px) {
    .formWrapp {
        width: 250px;
    }
}

.formControlLabel {
    padding-left: 1px;

    font-family: Montseratt;
    font-size: 14px;
    font-weight: 500;
}

.passwordRecoveryLinkBlock {
    margin-bottom: 40px;

    text-align: right;
}

.passwordRecoveryLink {
    font-size: 14px;
    font-weight: 700;
    line-height: 17.07px;
    color: #000000;
    text-decoration: none;
}

.haveAnAccountBlock {
    font-size: 14px;
    font-weight: 600;
    line-height: 24px;

    opacity: 0.5;
}

.navigateToLink {
    font-size: 16px;
    font-weight: 600;
    color: #366EFF;

}